import React from "react";
import numeral from 'numeral';

import styles from "./StakeCard.module.scss";
import classNames from "classnames";
import {useGlobalData} from "../../../hooks/useBNBCrown";
const StakeCard = (props) => {
  const { info } = props;
  const { plan, state, startDate, endDate, amount, earn, blue, percent } = info;
  const {data: globalData} = useGlobalData();
  return (
    <div className={classNames(styles.stakeCard, { [styles.blue]: blue })}>
      <div className={styles.planTitle}>
        <p className={styles.type}>{`Plan ${plan}`}</p>
        <p className={styles.state}>{state}</p>
        <p className={styles.time}>{`${startDate} - ${endDate}`}</p>
      </div>
      <div className={styles.planRate}>
        <p>{globalData && globalData.planInfo && globalData.planInfo[plan - 1].rate * 100}%</p>
      </div>
      <div className={styles.planDetail}>
        <div className={classNames(styles.infoItem, styles.left)}>
          <h1>{numeral(amount).format('0.[00]')}</h1>
          <p>Staked amount</p>
        </div>
        <div className={classNames(styles.infoItem, styles.right)}>
          <h1>{numeral(earn).format('0.[000]')}</h1>
          <p>Total Return</p>
        </div>
      </div>
      <div className={styles.progress}>
        <div className={styles.inner} style={{width: `${percent}%`}}/>
      </div>
    </div>
  );
};

export default StakeCard;
